.App {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;

}
.SuperBasic {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  width: 90%;
}

.container_ProfilePhoto {
    margin: 0 25px;
}
.container_InfoAndContact {
    margin: 0 25px;
}
.container_ProfilePhoto > div {
    background-size: cover;
    -webkit-box-shadow: 10px 5px 5px gray;
            box-shadow: 10px 5px 5px gray;
    -webkit-filter: gray;
            filter: gray;
    height: 150px;
    -webkit-filter: grayscale(100%);
    width: 150px;
}

.container_Info h2 {
  margin: 0;
  padding: 0;
}

.container_Icons a{
  margin-right: 10px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



